.container {
  margin: 0;
  padding: 0;
}

.profile-img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  object-fit: cover;
  text-align: center;
}
.greeting-profile {
  text-align: center;
  display: block;
  font-weight: 100;
}
.profile-card {
  border: 1px darkgray solid;
  position: absolute;
  border-radius: 3%;
  box-shadow: 6px 1px 17px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.5);
  text-align: center;
  margin-left: 10px;
  margin-bottom: 10px;
}
